//
// RTL Support
//
// scss-lint:disable NestingDepth, SelectorDepth
*[dir="rtl"] {
    direction: rtl;
    unicode-bidi: embed;

    ul {
        -webkit-padding-start: 0;
    }

    .ml-auto {
        margin-right: auto !important;
        margin-left: 0 !important;
    }

    .mr-auto {
        margin-right: initial !important;
        margin-left: auto !important;
    }

    .dropdown-menu-right {
        left: 0 !important;
        right: auto !important;
    }

    code {
        direction: ltr;
        unicode-bidi: isolate;
    }

    .ltr {
        direction: ltr;
        unicode-bidi: isolate;
    }

    .input-group-addon:not(:last-child) {
        border-left: 0 !important;
        border-right: 1px solid #c2cfd6 !important;
    }

    .form-control {
        + .input-group-addon:not(:first-child) {
            border-right: 0 !important;
            border-left: 1px solid #c2cfd6 !important;
        }
    }

    .card-header {
        .card-actions {
            top: 0;
            left: 0 !important;
        }

        > i {
            margin-left: 0.5rem !important;
            margin-right: 0 !important;

        }
    }

    table tr th {
        text-align: right;
    }

    //grid

    .mr-3 {
        margin-left: 1rem !important;
        margin-right: 0 !important;
    }

    .mr-4 {
        margin-left: 1.5rem !important;
        margin-right: 0 !important;
    }

    // Breadcrumb

    .breadcrumb-item {
        float: right;
    }

    .breadcrumb-menu {
        right: auto;
        left: $breadcrumb-padding-x;
    }

    // Dropdown
    .dropdown-item {
        text-align: right;

        i {
            margin-right: -10px;
            margin-left: 10px;
        }

        .badge {
            right: auto;
            left: 10px;
        }
    }

    //
    // Sidebar
    //
    .sidebar-hidden {

        .sidebar {
            margin-right: - $sidebar-width;
        }
    }

    .sidebar-fixed {

        .main, .app-footer {
            margin-right: $sidebar-width;
        }

        &.sidebar-hidden {

            .main, .app-footer {
                margin-right: 0;
            }
        }
    }

    .sidebar-minimized {
        .sidebar {
            flex: 0 0 $sidebar-minimized-width;
        }

        &.sidebar-hidden {

            .sidebar {
                margin-left: - $sidebar-minimized-width;
            }
        }

        &.sidebar-fixed {

            .main, .app-footer {
                margin-left: $sidebar-minimized-width;
                margin-right: $sidebar-minimized-width;
            }

            &.sidebar-hidden {

                .main, .app-footer {
                    margin-left: 0;
                }
            }
        }
    }

    //
    // Aside Menu
    //
    .aside-menu-hidden {

        .aside-menu {
            // margin-right: 0;
            margin-left: - $aside-menu-width;
        }
    }

    .aside-menu-off-canvas {
        .aside-menu {
            position: fixed;
            right: 0;
            z-index: $zindex-sticky - 1;
            height: 100%;

            .tab-content {
                height: calc(100vh - #{$aside-menu-nav-padding-y * 2 + $font-size-base} - #{$navbar-height});
            }
        }
    }

    // Sidebar Menu
    @include media-breakpoint-up(lg) {
        .sidebar-minimized {
            .nav-link {
                padding-right: 0 !important;

                i {
                    float: right !important;
                }
            }
        }
        .aside-menu-fixed {

            .aside-menu {
                right: auto;
                left: 0;
            }

            .main, .app-footer {
                //margin-right: 0;
                margin-left: $aside-menu-width;
            }

            &.aside-menu-hidden {

                .main, .app-footer {
                    margin-left: 0;
                }
            }
        }
    }
    @include media-breakpoint-down(lg) {

        .sidebar-mobile-show .sidebar {
            margin-left: -220px;

            margin-right: inherit;

        }
        .sidebar {
            margin-right: -220px;
        }
        .sidebar-mobile-show .main {
            margin-left: -220px !important;
            margin-right: 220px !important;
        }

        .aside-menu-fixed {

            .aside-menu {
                right: auto;
                left: 0;
            }

            .main, .app-footer {
                //margin-right: 0;
                margin-left: $aside-menu-width;
            }

            &.aside-menu-hidden {

                .main, .app-footer {
                    margin-right: 0;
                }
            }
        }
    }

    .sidebar {
        .sidebar-nav {
            direction: ltr;

            * {
                direction: rtl;
            }

            .nav {
                .nav-item {
                    .nav-link {
                        [class^="icon-"], [class*=" icon-"] {
                            margin-right: -4px;
                        }

                        .badge {
                            float: left;
                            margin-top: 2px;
                            // margin-left: 10px;
                        }

                        &.nav-dropdown-toggle {
                            &::before {
                                position: absolute;
                                right: auto !important;
                                left: $sidebar-nav-link-padding-x;
                                transform: rotate(180deg);
                            }
                        }
                    }

                    &.nav-dropdown {
                        &.open {
                            > .nav-link.nav-dropdown-toggle::before {
                                transform: rotate(90deg);
                            }
                        }
                    }
                }
            }
        }
    }

    // Horizontal bars
    .horizontal-bars {

        li {

            .bars {
                padding-right: 100px;
                padding-left: 0;

                .progress:first-child {
                    margin-bottom: 2px;
                }
            }
        }

        &.type-2 {

            li {

                i {
                    margin-right: 5px;
                    margin-left: $spacer;
                }

                .value {
                    float: left;
                    font-weight: 600;
                }

                .bars {
                    padding: 0;
                }
            }
        }
    }

    // Icon list
    .icons-list {

        li {
            position: relative;
            height: 40px;
            vertical-align: middle;

            i {
                float: left;
            }

            .desc {
                margin-right: 50px;
                margin-left: 0;

                .title {
                    font-size: 0.8em;
                }
            }

            .value {
                right: auto;
                left: 45px;
                text-align: left;

                strong {
                    display: block;
                    margin-top: -3px;
                }
            }

            .actions {
                right: auto;
                left: 10px;
            }
        }
    }

    // Callouts
    .callout {
        border: 0 solid $border-color;
        border-right-width: .25rem;

        .chart-wrapper {
            left: 0;
            float: left;
        }
    }

    .callout-default {
        border-right-color: $text-muted;
    }

    @each $color, $value in $theme-colors {
        &.callout-#{$color} {
            border-right-color: $value;
        }
    }
}
